.advantages {
	padding-top: 97px;
	padding-bottom: 65px;
}
.list-advantages {
	display: grid;
	grid-template-columns: 1fr 1fr;
	grid-template-rows: auto;
	gap: 20px;
}

.list-advantages__item {
	padding: 30px;
	display: flex;
	align-items: center;
}

.list-advantages__item.bg-1,
.list-advantages__item.bg-4 {
	background-color: #e6f2fe;
}
.list-advantages__item.bg-2,
.list-advantages__item.bg-3 {
	background-color: #fbebf5;
}
.advantages__icon {
	width: 100px;
	height: 100px;
	margin-right: 30px;
}
.list-advantages__text {
	max-width: 215px;
	font-size: 18px;
	color: #2e2e2e;
}

@media (max-width: 768px) {
	.advantages {
		padding-top: 40px;
		padding-bottom: 40px;
	}
	.list-advantages {
		grid-template-columns: 1fr;
	}
	.list-advantages__text {
		max-width: 100%;
	}
}
