.popup-fade {
	display: none;
}
.popup-fade:before {
	content: "";
	background: #000;
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	opacity: 0.7;
	z-index: 9999;
}
.popup {
	position: fixed;
	top: 20%;
	left: 50%;
	width: 500px;
	height: 398px;
	background: #ffffff;
	border: 0.5px solid #727272;
	border-radius: 15px;
	margin-left: -200px;
	border-radius: 4px;
	z-index: 99999;
	opacity: 1;
}
.popup-close {
	position: absolute;
	top: 10px;
	right: 10px;
}

.btn-sign-up {
	padding: 12px 48px;
	background-color: var(--color-primary);
	border: 2px solid transparent;
	border-radius: 30px;
	font-weight: 400;
	font-size: 24px;
	color: #ffffff;
	text-align: center;
	white-space: nowrap;
}
.btn-sign-up.nice-select.open,
.btn-sign-up:active,
.btn-sign-up:focus,
.btn-sign-up:hover {
	color: var(--color-primary);
	background-color: transparent;
	border-color: var(--color-primary);
	transition: all 0.3s ease;
}

.popup__header {
	padding: 20px 30px;
	border-bottom: 1px solid #cfcfcf;
	display: flex;
	align-items: center;
	justify-content: space-between;
}

.popup__slogan {
	font-size: 14px;
	color: #f56ca8;
}

.popup__text {
	font-size: 20px;
	color: #191919;
	margin-bottom: 24px;
}

.popup__inner {
	padding: 30px;
}

.form__group {
	margin-bottom: 24px;
}
.popup__form-tel {
	margin-bottom: 24px;
	width: 100%;
	padding: 18px;
	border: 1px solid #f56ca8;
	border-radius: 30px;
}
.btn-form {
	width: 100%;
	padding: 18px;
	color: #ffffff;
	background: #f56ca8;
	border-radius: 30px;
}
