.program {
	padding-top: 50px;
	padding-bottom: 50px;
}

.program__grid {
	display: grid;
	grid-template-columns: 1fr 1fr;
	gap: 40px;
}

.program__title {
	font-weight: 500;
	font-size: 28px;
	color: #2e2e2e;
	margin-bottom: 32px;
}

.program__subtitle {
	margin-bottom: 24px;
	font-weight: 500;
	font-size: 24px;
	color: #2e2e2e;
}

.program__grid .list-action {
	margin-bottom: 48px;
}

.program__column-left .btn-sign-up {
	margin-bottom: 60px;
}

.promo-group__title {
	font-size: 16px;
	color: #f56ca8;
	margin-bottom: 4px;
}

.promo-group__list {
	list-style: inside;
}
.promo-group__list-item {
	font-size: 16px;
	color: #000000;
}
.program__column-right .list-action__item::before {
	background-color: #c7a4cd;
}

.program__warning {
	background-image: url("../img/mithosis-bg.png");
	background-repeat: no-repeat;
	background-position: left center;
	background-size: auto;
}
.program__warning__text {
	max-width: 420px;
	padding-top: 62px;
	padding-bottom: 62px;
	padding-left: 53px;
	padding-right: 23px;
	font-weight: 400;
	font-size: 28px;
	line-height: 1.33;
	text-align: left;
	color: #ffffff;
}

@media (max-width: 878px) {
	.program__warning {
		background-image: none;
		background-color: #e83181;
		border-radius: 30px;
	}
}

@media (max-width: 420px) {
	.program__title {
		font-size: 24px;
		margin-bottom: 24px;
	}
}
