.welcome {
	background-image: url("../img/bg3.png");
	background-repeat: no-repeat;
	background-position: center;
	background-size: cover;
}

.welcome__wrapper {
	display: flex;
	flex-direction: row-reverse;
	justify-content: space-between;
}

.welcome-bg {
	align-self: flex-end;
	position: relative;
}
.bg-img {
	width: 500px;
	height: 717px;
}

.welcome__inner {
	max-width: calc(100% - 500px);
	padding-top: 68px;
	padding-bottom: 152px;
	position: relative;
	z-index: 11;
}

.welcome__title {
	font-weight: 700;
	font-size: 50px;
	line-height: 1.2;
	letter-spacing: 0.05em;
	color: var(--color-primary);
	text-transform: uppercase;
	margin-bottom: 54px;
}

.price-group {
	margin-bottom: 28px;
}

.price__old {
	font-weight: 600;
	font-size: 24px;
	text-align: left;
	text-decoration-line: line-through;
	color: #967ca0;
	margin-bottom: 8px;
	text-decoration: line-through;
	text-decoration-color: var(--color-primary);
}

.price__new {
	font-weight: 700;
	font-size: 38px;
	color: var(--color-primary);
}

.complex__title {
	font-size: 24px;
	color: #2e2e2e;
	margin-bottom: 16px;
}

.list-action {
	margin-bottom: 44px;
}
.list-action__item {
	font-size: 20px;
	color: #2e2e2e;
	padding-left: 22px;
	position: relative;
}
.list-action__item:not(:last-child) {
	margin-bottom: 15px;
}

.list-action__item::before {
	position: absolute;
	top: 3px;
	left: 0;
	content: "";
	width: 14px;
	height: 14px;
	background-color: var(--color-primary);
	border-radius: 50%;
}

@media (max-width: 878px) {
	.welcome-bg {
		display: none;
	}
	.welcome__inner {
		margin: 0 auto;
		max-width: 100%;
	}
}

@media (max-width: 768px) {
	.welcome__inner {
		padding-top: 40px;
		padding-bottom: 60px;
	}
}

@media (max-width: 4680px) {
	.welcome__inner {
		padding-top: 40px;
		padding-bottom: 10px;
	}
	.btn-sign-up {
		padding: 10px 20px;
		font-size: 18px;
	}
	.welcome__title {
		font-size: 30px;
		margin-bottom: 20px;
	}
}
