.references {
	padding-top: 50px;
	padding-bottom: 50px;
}

.references__title {
	margin: 0 auto;
	max-width: 930px;
	margin-bottom: 60px;
	font-weight: 500;
	font-size: 40px;
	color: #2e2e2e;
	text-align: center;
}

.references__list {
	display: grid;
	grid-template-columns: 40% 60%;
	gap: 40px;
}
.references__list-item {
	align-self: start;
	padding: 24px 32px;
	font-size: 20px;
	color: #e83181;
	&:nth-child(odd) {
		border-radius: 0px 30px 30px 30px;
	}
	&:nth-child(even) {
		border-radius: 30px 30px 0px 30px;
	}
}

.item-1,
.item-4 {
	background-color: #ece8f7;
}
.item-2,
.item-5 {
	background-color: #fbebf5;
}
.item-3,
.item-6 {
	background-color: #e6f2fe;
}

.item-1 {
	position: relative;
	max-width: 355px;

	&::before {
		position: absolute;
		top: -60%;
		right: -5%;
		content: "";
		width: 75px;
		height: 78px;
		background-image: url("../img/group-stars.png");
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
		transform: rotateZ(90deg);
	}
}
.item-2 {
	max-width: 440px;
	justify-self: end;
}
.item-3 {
	position: relative;
	top: -20px;
	max-width: 395px;
}
.item-4 {
	position: relative;
	max-width: 670px;
	justify-self: end;
	&::before {
		position: absolute;
		top: -25%;
		left: -3%;
		content: "";
		width: 67px;
		height: 67px;
		background-image: url("../img/star.png");
		background-repeat: no-repeat;
		background-position: center;
		background-size: cover;
	}
}

@media (max-width: 768px) {
	.references {
		padding-top: 20px;
		padding-bottom: 20px;
	}
	.references__title {
		max-width: 100%;
		font-size: 30px;
	}
	.program__grid {
		grid-template-columns: 1fr;
	}
	.program__warning__text {
		max-width: 100%;
		padding-top: 25px;
		padding-bottom: 25px;
		padding-left: 25px;
		padding-right: 25px;
		font-size: 22px;
	}
}

@media (max-width: 420px) {
	.references__list {
		grid-template-columns: 1fr;
	}
}
